<template>
    <el-container class="home-container">
        <!-- 头部区域 -->
        <el-header>
            <div>
                <img src="../assets/HNXX.jpg" alt="">
                <span>日报管理系统</span>
            </div>
            <el-button type="info" @click="logout">退出</el-button>
        </el-header>
        <!-- 页面主体区域 -->
        <el-container>
            <!-- 侧边栏 -->
            <el-aside :width="isCollapse ? '64px' : '200px'">
                <!-- 折叠按钮条 -->
                <div class="toggle-button" @click="toggleCollapse">|||</div>
                <!-- 侧边栏菜单区域 -->
                <!-- :default-active="activePath" -->
                <el-menu background-color="#333744"
                        text-color="#fff"
                        active-text-color="#409eff"
                        :unique-opened="true" :collapse="isCollapse"
                        :collapse-transition="false"
                        :router="true"
                        :default-active="this.$route.path">
                    <!-- 一级菜单 -->
                    <el-submenu  :index="item.id+''" v-for="item in menulist" :key="item.id">
                        <!-- 一级菜单的模板区域 -->
                        <template slot="title">
                            <!-- 图标 -->
                            <i :class="item.icon"></i>
                            <!-- 文本 -->
                            <span>{{item.name}}</span>
                        </template>

                        <!-- 二级菜单 -->
                        <el-menu-item :index="'/'+item1.path" v-for="item1 in item.children"
                                      :key="item1.id" @click="saveNavState('/'+item1.path)">
                            <template slot="title">
                                <!-- 图标 -->
                                <i class="el-icon-menu"></i>
                                <!-- 文本 -->
                                <span>{{item1.name}}</span>
                            </template>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <!-- 右侧内容主体 -->
            <el-main>
                <!-- 路由占位符 -->
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    export default {

        data() {
          return {
              // 左侧菜单
              menulist: [],
              iconsObj: {
                  100:'iconfont icon-user',   // 用户管理
                  200:'iconfont icon-tijikongjian',   // 权限管理
                  300:'iconfont icon-shangpin',   // 日报管理
                  400:'iconfont icon-danju',   // 项目管理
                  500:'iconfont icon-baobiao'    // 数据统计
              },
              // 是否折叠
              isCollapse: false,
              // 被激活的链接地址
              activePath:''
          }
        },

        created(){
            this.getMenuList()
            // this.activePath = window.sessionStorage.getItem('activePath')
        },

        methods: {
            logout(){
                window.sessionStorage.clear()
                this.$router.push('/login')
            },
            // 获取所有的菜单
            async getMenuList(){
                const _this = this
                this.$http.get('menu/menus').then(function (resp) {
                    console.log(resp)
                    _this.menulist = resp.data
                })
            },
            // 点击按钮,切换菜单的折叠与展开
            toggleCollapse(){
                this.isCollapse = !this.isCollapse
            },
            // 保存链接的激活状态 Ps: :default-active="this.$route.path"
            saveNavState(activePath){
                window.sessionStorage.setItem('activePath',activePath)
                this.activePath = activePath
            }
        }
    }
</script>

<style lang="less" scoped>
    .el-header{
        background-color: #373d41;
        display: flex;
        justify-content: space-between;     // 均匀分配
        padding-left: 0;
        align-items: center;       // 元素居中[退出按钮居中]
        color: #fff;
        font-size: 20px;
        > div {
            display: flex;
            align-items: center;
            span{
                margin-left: 15px;
            }
        }
        img{
            width: 60px;
            height: 60px;
            border-radius: 50%;
            background-color: #eee;
        }
    }
    .home-container {
        height: 100%;
    }

    .el-aside {
        background-color: #333744;
        .el-menu {
            border-right: none;
            background-color: #333744;
        }
    }

    .el-main {
        background-color: #eaedf1;
    }

    .iconfont {
        margin-right: 10px;
    }

    .toggle-button {
        background-color: #4A5064;
        font-size: 10px;
        line-height: 24px;
        color: #fff;
        text-align: center;
        letter-spacing: 0.2em;      // 字间距
        cursor: pointer;        // 聚焦样式
    }

    .el-submenu{
        background-color: #333744;
    }


</style>