<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item><a href="/">角色管理</a></el-breadcrumb-item>
            <el-breadcrumb-item>角色列表</el-breadcrumb-item>
        </el-breadcrumb>

        <!-- 卡片视图 -->
        <el-card>
            <!-- 添加角色按钮区域 -->
            <el-row>
                <el-col>
                    <el-button type="primary" @click="addDialogVisible = true">添加角色</el-button>
                </el-col>
            </el-row>

            <!-- 角色列表区 -->
            <el-table :data="rolelist" border stripe>
                <!-- 展开列 -->
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <!-- 栅格组件 一行24个 -->
                        <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
                            <!-- 渲染一级权限 -->
                            <el-col :span="5">
                                <el-tag closable @close="removeRightById(scope.row, item1.id)">{{item1.name}}</el-tag>
                                <i class="el-icon-caret-right"></i>
                            </el-col>
                            <!-- 渲染二,三级级权限 -->
                            <el-col :span="19">
                                <!-- 通过for循环嵌套渲染二级权限 -->
                                <el-row :class="[i2 === 0 ? '':'bdtop', 'vcenter']" v-for="(item2, i2) in item1.children" :key="item2.id">
                                    <el-col :span="6">
                                        <el-tag type="success" closable @close="removeRightById(scope.row, item2.id)">{{item2.name}}</el-tag>
                                        <i class="el-icon-caret-right"></i>
                                    </el-col>
                                    <el-col :span="18">
                                        <el-tag type="warning" v-for="(item3, i3) in item2.children" :key="item3.id" closable @close="removeRightById(scope.row, item3.id)">
                                            {{item3.name}}
                                        </el-tag>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                    </template>
                </el-table-column>
                <!-- 索引列 -->
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column label="角色名称" prop="name"></el-table-column>
                <el-table-column label="角色描述" prop="description"></el-table-column>
                <el-table-column label="操作" width="300px">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)">编辑</el-button>
                        <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeRoleById(scope.row.id)">删除</el-button>
                        <el-button size="mini" type="warning" icon="el-icon-setting">分配权限</el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!-- 添加角色对话框 -->
            <el-dialog title="添加角色" :visible.sync="addDialogVisible" width="60%" @close="addDialogClosed">
                <!-- 内容主体 -->
                <el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="100px" size="small">
                    <el-form-item label="角色名" prop="name">
                        <el-input placeholder="请输入角色名" v-model="addForm.name" prop="name"></el-input>
                    </el-form-item>
                    <el-form-item label="角色描述" prop="description">
                        <el-input placeholder="请输入角色描述" v-model="addForm.description" prop="description"></el-input>
                    </el-form-item>
                </el-form>
                <!-- 底部区域 -->
                <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="addDialogForm('addFormRef')">确 定</el-button>
            </span>
            </el-dialog>

            <!-- 修改角色对话框 -->
            <el-dialog title="修改用户" :visible.sync="editDialogVisible" width="60%" @close="editDialogClosed">
                <!-- 内容主体 -->
                <el-form ref="editFormRef" :model="editForm" label-width="70px" size="small">
                    <el-form-item label="角色名" prop="name">
                        <el-input  v-model="editForm.name" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="角色描述" prop="description">
                        <el-input  v-model="editForm.description"></el-input>
                    </el-form-item>
                </el-form>
                <!-- 底部区域 -->
                <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editDialogForm('editFormRef')">确 定</el-button>
            </span>
            </el-dialog>

        </el-card>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                // 所有角色列表数据
                rolelist: [],
                // 控制添加角色对话框的显示与隐藏
                addDialogVisible: false,
                // 添加角色的表单数据
                addForm:{
                    name:'',
                    description:''
                },
                // 添加表单的验证规则对象
                addFormRules:{
                    name: [{
                        required: true, message: '请输入角色名', trigger: 'blur'
                    }],
                    description: [{
                        required: true, message: '请输入角色描述', trigger: 'blur'
                    }]
                },
                // 修改角色对黄框的显示与隐藏
                editDialogVisible:false,
                // 修改角色的表单数据
                editForm:{
                    name:'',
                    description:'',
                    rid:''
                },
                // 修改表单的验证规则
                editFormRules: {
                    name:[],
                    description:[]
                }

            }
        },
        created() {
            this.getRolesList()
        },
        methods: {
            // 获取角色列表
            getRolesList() {
                const _this = this
                axios.get('role/roles').then(function (resp) {
                    console.log(resp)
                    _this.rolelist = resp.data
                })
            },
            // 添加角色对话框关闭事件
            addDialogClosed(){
                this.$refs.addFormRef.resetFields()
            },
            // 添加角色事件
            addDialogForm(addFormRef){
                const _this = this
                this.$refs[addFormRef].validate((valid) => {
                    if (valid) {
                        axios.post('role/roles/add', this.addForm).then(function (resp) {
                            console.log(resp)
                            if (resp.data == 'success') {
                                _this.$message.success('添加角色成功！！！')
                                _this.addDialogVisible = false
                                _this.getRolesList()
                            }else{
                                _this.$message.error('添加角色失败！！！');
                            }
                        })
                    } else {
                        return false;
                    }
                });
            },
            //
            // 展示编辑用户的对话框
            showEditDialog(id){
                const _this = this
                axios.get('role/roles/findById'+ '/'+id).then(function (resp) {
                    console.log(resp)
                    _this.editForm = resp.data
                    _this.editDialogVisible = true
                })
            },

            // 修改角色对话狂关闭事件
            editDialogClosed(){
                this.$refs.editFormRef.resetFields()
            },
            // 修改角色
            editDialogForm(editFormRef){
                const _this = this
                this.$refs[editFormRef].validate((valid) => {
                    if (valid) {
                        axios.put('role/roles/edit', this.editForm).then(function (resp) {
                            console.log(resp)
                            if (resp.data == 'success') {
                                _this.$message.success('修改角色成功！！！')
                                _this.editDialogVisible = false
                                _this.getRolesList()
                            }else{
                                _this.$message.error('修改角色失败！！！');
                            }
                        })
                    } else {
                        return false;
                    }
                });
            },
            // 根据id删除角色信息
            async removeRoleById(id){
                const _this = this
                // 弹框询问是否删除数据
                const  confirmResult = await this.$confirm('此操作将永久删除该角色信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).catch(err => {
                    return err
                })
                // 如果用户确定删除,则返回值为字符串 confirm
                // 如果用户取消了删除,则返回值为字符串 cancel
                // console.log(confirmResult)
                if(confirmResult !== 'confirm'){
                    return this.$message.info('已取消')
                } else{
                    axios.delete('/role/roles/deleteById/' + id).then(function (resp) {
                        console.log(resp)
                        _this.$message.success('删除角色成功！！！')
                        _this.getRolesList()
                    })
                }
            },
            // 根据id删除对应的权限
            async removeRightById(id){
                // 弹框提示是否删除
                const confirmResult = await this.$confirm('此操作将移除该角色此该权限, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).catch(err => {
                    return err
                })
                // 如果用户确定删除,则返回值为字符串 confirm
                // 如果用户取消了删除,则返回值为字符串 cancel
                // console.log(confirmResult)
                if(confirmResult !== 'confirm'){
                    return this.$message.info('已取消')
                } else{
                    this.$message.error('该功能尚未完善')
                    // axios.delete(`role/${role.id}/right/${rightId}`).then(function (resp) {
                    //     console.log(resp)
                    //     _this.$message.success('删除角色成功！！！')
                    //
                    //     _this.getRolesList()
                    //     role.children = resp.data
                    // })
                }
            }
        }
    }
</script>

<style lang="less" scoped>

    .el-tag{
        margin: 7px;
    }
    .bdtop{
        border-top: 1px solid #eeeeee ;
    }

    .bdbottom{
        border-bottom: 1px solid #eeeeee;
    }

    .vcenter{
        display: flex;
        align-items: center;
    }

</style>