<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item><a href="/">用户管理</a></el-breadcrumb-item>
            <el-breadcrumb-item>用户列表</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区域 -->
        <el-card class="box-card">
            <!-- 搜索与添加区域 -->
            <!-- 栅格 -->
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
                    <el-button slot="append" icon="el-icon-search" @click="queryByKey" ></el-button>
                </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible = true">添加用户</el-button>
                </el-col>
            </el-row>

            <!-- 用户列表区域 -->
            <el-table :data="userData" border stripe style="width: 100%" >
                <el-table-column :show-overflow-tooltip="true"  width="30" label="#" type="index" fixed="left"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="100" label="姓名" prop="name"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="100" label="用户名" prop="username"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="100" label="密码" prop="password"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="100" label="角色" prop="role"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="60" label="年龄" prop="age"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="60" label="性别" prop="sex"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="130" label="手机号码" prop="mobile"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="130" label="QQ" prop="qq"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="100" label="邮箱" prop="email"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="120" label="创建时间" prop="createTime"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="120" label="修改时间" prop="updateTime"></el-table-column>
                <el-table-column :show-overflow-tooltip="true" align="center"  width="100" label="状态">
                    <!-- 作用域插槽 -->
                    <template slot-scope="scope">
                        <el-switch v-model="scope.row.active" @change="userIsActive(scope.row)">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="操作" align="center" fixed="right" width="200">
                    <template slot-scope="scope">
                        <!-- 修改按钮 -->
                        <el-tooltip effect="dark" content="修改" placement="top" :enterable="false">
                            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                        </el-tooltip>
                        <!-- 删除按钮 -->
                        <el-tooltip effect="dark" content="删除" placement="top" :enterable="false">
                            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
                        </el-tooltip>
                        <!-- 分配角色按钮 -->
                        <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
                            <el-button type="warning" icon="el-icon-setting" size="mini"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    align="center"
                    @size-change="handleSizeChange"
                    @current-change="currentPage"
                    :page-size="queryInfo.size"
                    layout="total, prev, pager, next, jumper"
                    :total="queryInfo.total">
            </el-pagination>
        </el-card>

        <!-- 添加用户对话框 -->
        <el-dialog title="添加用户" :visible.sync="addDialogVisible" width="60%" @close="addDialogClosed">
            <!-- 内容主体 -->
            <el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="70px" size="small">
                <el-form-item label="姓名" prop="name">
                    <el-input placeholder="请输入姓名" v-model="addForm.name" prop="name"></el-input>
                </el-form-item>
                <el-form-item label="用户名" prop="username">
                    <el-input placeholder="请输入用户名" v-model="addForm.username" prop="username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input placeholder="请输入密码" type="password" v-model="addForm.password" prop="password"></el-input>
                </el-form-item>
                <el-form-item label="年龄" prop="age">
                    <el-input placeholder="请输入年龄" v-model="addForm.age" prop="age"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                    <el-input placeholder="请输入性别" v-model="addForm.sex" prop="sex"></el-input>
                </el-form-item>
                <el-form-item label="手机" prop="mobile">
                    <el-input placeholder="请输入手机" v-model="addForm.mobile" prop="mobile"></el-input>
                </el-form-item>
                <el-form-item label="QQ" prop="qq">
                    <el-input placeholder="请输入QQ" v-model="addForm.qq" prop="qq"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input placeholder="请输入邮箱" v-model="addForm.email" prop="email"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="addDialogForm('addFormRef')">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 修改用户对话框 -->
        <el-dialog title="修改用户" :visible.sync="editDialogVisible" width="60%" @close="editDialogClosed">
            <!-- 内容主体 -->
            <el-form ref="editFormRef" :model="editForm" label-width="70px" size="small">
                <el-form-item label="姓名" prop="name">
                    <el-input placeholder="请输入姓名" v-model="editForm.name" disabled></el-input>
                </el-form-item>
                <el-form-item label="用户名" prop="username">
                    <el-input placeholder="请输入用户名" v-model="editForm.username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input placeholder="请输入密码" v-model="editForm.password"                                                                                                                                                                                                                                                                                                                                            ></el-input>
                </el-form-item>
                <el-form-item label="年龄" prop="age">
                    <el-input placeholder="请输入年龄" v-model="editForm.age"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                    <el-input placeholder="请输入性别" v-model="editForm.sex"></el-input>
                </el-form-item>
                <el-form-item label="手机" prop="mobile">
                    <el-input placeholder="请输入手机" v-model="editForm.mobile"></el-input>
                </el-form-item>
                <el-form-item label="QQ" prop="qq">
                    <el-input placeholder="请输入QQ" v-model="editForm.qq"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input placeholder="请输入邮箱" v-model="editForm.email"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editDialogForm('editFormRef')">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        data() {
          // 校验规则变量定义
          // 验证邮箱规则
          var chechEmail = (rule, value, cb) => {}
          // 验证手机号规则
          var checkMobile = (rule, value, cb) => {}

          return{
              // 获取用户参数列表的参数
              queryInfo:{
                  query:'',
                  page:'',
                  size: 6,
                  total: 7
              },
              userData: [{
                  id:'',
                  name:'',
                  username:'',
                  password:'',
                  age:'',
                  sex:'',
                  mobile:'',
                  qq:'',
                  email:'',
                  createTime:'',
                  updateTime:'',
                  role:'',
                  active:''
              }],

              // 控制添加用户对话框的显示与隐藏
              addDialogVisible: false,
              // 添加用户的表单数据
              addForm:{
                  name:'',
                  username:'',
                  password:'',
                  age:'',
                  sex:'',
                  mobile:'',
                  qq:'',
                  email:'',
              },
              // 添加表单的验证规则对象
              addFormRules:{
                  name: [{
                      required: true, message: '请输入姓名', trigger: 'blur'
                  }],
                  username: [{
                      required: true, message: '请输入用户名', trigger: 'blur'
                  }],
                  password: [{
                      required: true, message: '请输入密码', trigger: 'blur'
                  }],
                  mobile:[
                      {required: true, message: '请输入电话号码', trigger: 'blur'},
                      {min: 6, max: 11, message: '请输入10~12位数字'}
                  ]
              },

              // 控制修改对话框的显示与隐藏
              editDialogVisible: false,
              // 控制修改的表单数据
              editForm:{},
              // 修改表单验证规则
              editFormRules:{}
          }
        },
        // 生命周期函数
        created() {
            this.getUserList()
        },
        methods: {
            // 获取初始化用户列表
            getUserList(){
                const _this = this
                axios.get('user/users/0/'+ this.queryInfo.size).then(function (resp) {
                    console.log(resp)
                    _this.userData = resp.data.content
                    _this.queryInfo.size = resp.data.size
                    _this.queryInfo.total = resp.data.totalElements
                })
            },
            // 改变页面大小
            handleSizeChange(newSize){
                const _this = this
                this.queryInfo.size = newSize;
                axios.get('user/users/'+ this.queryInfo.page + '/' + this.queryInfo.size).then(function (resp) {
                    console.log(resp)
                    _this.userData = resp.data.content
                    _this.queryInfo.size = resp.data.size
                    _this.queryInfo.total = resp.data.totalElements
                })
            },
            // 分页跳转事件
            currentPage(newPage) {
                const _this = this
                axios.get('user/users/' + (newPage - 1) + '/' + this.queryInfo.size).then(function (resp) {
                    console.log(resp)
                    _this.userData = resp.data.content
                    _this.queryInfo.size = resp.data.size
                    _this.queryInfo.total = resp.data.totalElements
                })
            },
            // 监听状态开关
            userIsActive(userInfo){
                const _this = this
                axios.put('user/users/editActive',userInfo).then(function (resp) {
                    console.log(resp)
                    if(resp.data == "success" || resp.status == 200){
                        _this.$message.success('更新用户状态成功！！！')
                    }else{
                        userInfo.active = !userInfo.active
                        _this.$message.error('更新用户状态失败！！！')
                    }
                })
            },
            // 根据字段搜索
            queryByKey(){
                const _this = this
                this.$message.error("!!!该功能未完善!!!")
                axios.get('user/users/query/'+this.queryInfo.query).then(function (resp) {
                    console.log(resp)
                    _this.userData = resp.data
                })
            },
            // 提交添加用户
            addDialogForm(addFormRef) {
                const _this = this
                this.$refs[addFormRef].validate((valid) => {
                    if (valid) {
                        axios.post('user/users/add', this.addForm).then(function (resp) {
                            console.log(resp)
                            if (resp.data == 'success') {
                                _this.$message.success('添加用户成功！！！')
                                _this.addDialogVisible = false
                                _this.getUserList()
                            }else{
                                _this.$message.error('添加用户失败！！！');
                            }
                        })
                    } else {
                        return false;
                    }
                });
            },
            // 监听添加用户对话框的关闭事件
            addDialogClosed() {
                this.$refs.addFormRef.resetFields()
            },
            // 展示编辑用户的对话框
            showEditDialog(id){
                const _this = this
                axios.get('user/users/findById'+ '/'+id).then(function (resp) {
                    console.log(resp)
                    _this.editForm = resp.data
                    _this.editDialogVisible = true
                })
            },
            // 修改对话框验证规则重置
            editDialogClosed(){
                this.$refs.editFormRef.resetFields()
            },
            // 修改用户信息
            editDialogForm(editFormRef){
                const _this = this
                this.$refs[editFormRef].validate((valid) => {
                    if (valid) {
                        axios.post('user/users/edit', this.editForm).then(function (resp) {
                            console.log(resp)
                            if (resp.data == 'success') {
                                _this.$message.success('修改用户成功！！！')
                                _this.editDialogVisible = false
                                _this.getUserList()
                            }else{
                                _this.$message.error('修改用户失败！！！');
                            }
                        })
                    } else {
                        return false;
                    }
                });
            },
            // 根据id删除用户信息
            async removeUserById(id){
                const _this = this
                // 弹框询问是否删除数据
                const  confirmResult = await this.$confirm('此操作将永久删除该用户信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).catch(err => {
                    return err
                })
                // 如果用户确定删除,则返回值为字符串 confirm
                // 如果用户取消了删除,则返回值为字符串 cancel
                // console.log(confirmResult)
                if(confirmResult !== 'confirm'){
                    return this.$message.info('已取消')
                } else{
                    axios.delete('user/users/deleteById/' + id).then(function (resp) {
                        console.log(resp)
                        _this.$message.success('删除用户成功！！！')
                        _this.getUserList()
                    })
                }
            },


        },
    }
</script>

<style lang="less" scoped>
</style>