<template>
    <div class="login_container">
        <div class="login_box">
            <!-- 头像区域 -->
            <div class="avatar_box">
                <img src="../assets/HNXX.jpg" alt="">
            </div>
            <!-- 登录表单区域 -->
            <!-- ref 相当于返回 Dom 对象 -->
            <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
                <!-- 用户名 -->
                <el-form-item prop="username">
                    <el-input v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
                </el-form-item>
                <!-- 密码 -->
                <el-form-item prop="password">
                    <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" type="password"></el-input>
                </el-form-item>
                <!-- 按钮区域 -->
                <el-form-item class="btns">
                    <el-button type="primary" @click="login">登录</el-button>
                    <el-button  type="info" @click="resetLoginForm">重置</el-button>
                </el-form-item>
            </el-form>

        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                //这是登录表单的数据绑定对象
                loginForm:{
                    username: 'test',
                    password: '123456'
                },
                // 这是表单的验证规则对象
                loginFormRules: {
                    // 验证用户名是否合法
                    username: [
                        { required: true, message: '请输入登录名称', trigger: 'blur' },
                        { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
                    ],
                    // 验证密码是否合法
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' },
                        { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
                    ]
                }
            };
        },
        methods: {
            // 点击重置按钮，重置登录表单
            resetLoginForm() {
                this.$refs.loginFormRef.resetFields()
            },
            // 登录表单预验证
            login() {
                const _this = this
                // async 异步设置
                this.$refs.loginFormRef.validate(async valid => {
                   // console.log(valid);
                    if(!valid) return;
                    axios.post('/user/login/'+ this.loginForm.username + '/' + this.loginForm.password).then(function (resp) {
                        console.log(resp.data)
                        if (resp.data){
                            _this.$message.success('登录成功！！！')
                            window.sessionStorage.setItem("state",true);
                            _this.$router.push('/home')
                        }else{
                            _this.$message.error('登录失败！！！')
                        }
                    })
                });
            }
        }
    };
</script>

<!--此处 lang="less" 表示支持 less 语法-->
<!--scoped -- 当前组件 组件样式生效区间-->
<style lang="less" scoped>
    .login_container{
        background-color: #2b4b6b;
        height: 100%;
    }
    .login_box{
        width: 450px;
        height: 300px;
        background-color: #fff;
        border-radius: 3px;
        position: absolute;     //绝对定位 以左上角为定位点
        left: 50%;      //左侧 50%
        top: 50%;       //上方 50%
        transform: translate(-50%,-50%);       //居中
        // less 语法嵌套
        .avatar_box{
            height: 130px;
            width: 130px;
            border: 1px solid #eee;
            border-radius: 50%;
            padding: 10px;      //内边距
            box-shadow: 0 0 10px #dddddd;       //阴影 向外扩散 10px
            position: absolute;     //图像盒子绝对定位（参照父元素）
            left: 50%;      //距离左边 50%
            transform: translate(-50%, -50%);     //横向移动自身大小一半，纵向移动自身大小一半
            background-color: #fff;
            img{
                width: 100%;
                height: 100%;
                border-radius: 50%;
                background-color: #eee;
            }
        }
    }

    .login_form{
        position: absolute;
        bottom: 0;      // 底部对齐
        width: 100%;
        padding: 0 20px;
        box-sizing: border-box;
    }

    .btns{
        display: flex;      //弹性盒模型
        justify-content: flex-end;      // 横轴具右对齐

    }


</style>