<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item><a href="/">日报管理</a></el-breadcrumb-item>
            <el-breadcrumb-item>日报列表</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区域 -->
        <el-card class="box-card">
            <!-- 搜索与添加区域 -->
            <!-- 栅格 -->
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getReportList">
                        <el-button slot="append" icon="el-icon-search" @click="queryByKey" ></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible = true">添加日报</el-button>
                </el-col>
            </el-row>
            <!-- 日报列表区域 -->
            <el-table header-align="center" :data="reportData" border style="width: 100%" border stripe>
                <!-- 展开列 -->
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <el-form label-position="left" inline class="el-table-expand">
                            <el-row>
                                <el-col :span="2"><el-form-item label="工作内容"></el-form-item></el-col>
                                <el-col :span="10"><span>{{ scope.row.contents }}</span></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="2"><el-form-item label="遇到的困难"></el-form-item></el-col>
                                <el-col :span="10"><span>{{ scope.row.trouble }}</span></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="2"><el-form-item label="解决办法"></el-form-item></el-col>
                                <el-col :span="10"><span>{{ scope.row.method }}</span></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="2"><el-form-item label="今日心得"></el-form-item></el-col>
                                <el-col :span="10"><span>{{ scope.row.harvest }}</span></el-col>
                            </el-row>
                            <el-row>
                                <el-col :span="2"><el-form-item label="明日计划"></el-form-item></el-col>
                                <el-col :span="10"><span>{{ scope.row.plan }}</span></el-col>
                            </el-row>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column  type="index" label="#" width="30"></el-table-column>
                <el-table-column align="center" prop="title" label="日报标题" width="130"></el-table-column>
                <el-table-column align="center" prop="user" label="用户名" width="100"></el-table-column>
                <el-table-column align="center" prop="time" label="日报时间" width="120"></el-table-column>
                <el-table-column  align="center" label="日报内容" width="600">
                    <el-table-column :show-overflow-tooltip="true" align="center" label="工作内容" prop="contents" width="150"></el-table-column>
                    <el-table-column :show-overflow-tooltip="true" align="center" label="遇到的困难" prop="trouble" width="150"></el-table-column>
                    <el-table-column :show-overflow-tooltip="true" align="center" label="解决办法" prop="method" width="150"></el-table-column>
                    <el-table-column :show-overflow-tooltip="true" align="center" label="今日心得" prop="harvest" width="150"></el-table-column>
                    <el-table-column :show-overflow-tooltip="true" align="center" label="明日计划" prop="plan" width="150"></el-table-column>
                </el-table-column>
<!--                <el-table-column align="center" prop="group" label="组名" width="80"></el-table-column>-->
                <el-table-column label="操作" align="center" fixed="right" width="200">
                    <template slot-scope="scope">
                        <!-- 修改按钮 -->
                        <el-tooltip effect="dark" content="修改" placement="top" :enterable="false">
                            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                        </el-tooltip>
                        <!-- 删除按钮 -->
                        <el-tooltip effect="dark" content="删除" placement="top" :enterable="false">
                            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeReportByIdvue(scope.row.id)"></el-button>
                        </el-tooltip>
                        <!-- 分配角色按钮 -->
                        <el-tooltip effect="dark" content="设置" placement="top" :enterable="false">
                            <el-button type="warning" icon="el-icon-setting" size="mini"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    align="center"
                    @size-change="handleSizeChange"
                    @current-change="currentPage"
                    :page-size="queryInfo.size"
                    layout="total, prev, pager, next, jumper"
                    :total="queryInfo.total">
            </el-pagination>
        </el-card>

        <!-- 添加日报对话框 -->
        <el-dialog title="添加日报" :visible.sync="addDialogVisible" width="70%" @close="addDialogClosed">
            <template>
                <el-form  :model="addForm" status-icon :rules="addFormRules" ref="addFormRef" label-width="100px" class="demo-ruleForm">

                    <el-form :inline="true" class="demo-form-inline" :model="addForm" status-icon :rules="addFormRules" ref="ruleForm" label-width="100px">
                        <el-form-item  label="标题" prop="title" class="demo-form-inline">
                            <el-input style="width: 250px" type="text"  v-model="addForm.title"></el-input>
                        </el-form-item>

                        <el-form-item  label="用户名" width="150px" prop="user" >
                            <el-input style="width: 100px" type="text"  v-model="addForm.user"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form-item label="工作内容" prop="content"><el-input type="textarea" :rows="2" v-model="addForm.contents"></el-input></el-form-item>
                    <el-form-item label="遇到的困难" prop="trouble"><el-input type="textarea" :rows="2" v-model="addForm.trouble"></el-input></el-form-item>
                    <el-form-item label="解决办法" prop="method"><el-input type="textarea" :rows="2" v-model="addForm.method"></el-input></el-form-item>
                    <el-form-item label="今日心得" prop="harvest"><el-input type="textarea" :rows="2" v-model="addForm.harvest"></el-input></el-form-item>
                    <el-form-item label="明日计划" prop="plan"><el-input type="textarea" :rows="2" v-model="addForm.plan"></el-input></el-form-item>
                    <el-form-item label="日报时间">
                        <el-col :span="11">
                            <el-date-picker type="date" placeholder="选择日期" v-model="addForm.time" style="width: 100%;"></el-date-picker>
                        </el-col>
                        <el-col class="line" :span="2">-
                        </el-col>
                    </el-form-item>

                    <el-form-item align="center">
                        <el-button type="primary" @click="addDialogForm('addFormRef')">提交</el-button>
                        <el-button @click="addDialogVisible = false">取消</el-button>
                    </el-form-item>
                </el-form>
            </template>
        </el-dialog>

        <!-- 修改日报对话框 -->
        <el-dialog title="修改日报" :visible.sync="editDialogVisible" width="70%" @close="editDialogClosed">
            <template>
                <el-form  :model="editForm" status-icon :rules="editFormRules" ref="editFormRef" label-width="100px">

                    <el-form :inline="true" class="demo-form-inline" :model="editForm" status-icon :rules="editFormRules" ref="editFormRef" label-width="100px">
                        <el-form-item  label="标题" prop="title" class="demo-form-inline">
                            <el-input style="width: 250px" type="text"  v-model="editForm.title"></el-input>
                        </el-form-item>
                        <el-form-item  label="用户名" width="150px" prop="user" >
                            <el-input style="width: 100px" type="text"  v-model="editForm.user"></el-input>
                        </el-form-item>
                    </el-form>
                    <el-form-item label="工作内容" prop="content"><el-input type="textarea" :rows="2" v-model="editForm.contents"></el-input></el-form-item>
                    <el-form-item label="遇到的困难" prop="trouble"><el-input type="textarea" :rows="2" v-model="editForm.trouble"></el-input></el-form-item>
                    <el-form-item label="解决办法" prop="method"><el-input type="textarea" :rows="2" v-model="editForm.method"></el-input></el-form-item>
                    <el-form-item label="今日心得" prop="harvest"><el-input type="textarea" :rows="2" v-model="editForm.harvest"></el-input></el-form-item>
                    <el-form-item label="明日计划" prop="plan"><el-input type="textarea" :rows="2" v-model="editForm.plan"></el-input></el-form-item>
                    <el-form-item label="日报时间">
                        <el-col :span="11">
                            <el-date-picker type="date" placeholder="选择日期" v-model="editForm.time" style="width: 100%;"></el-date-picker>
                        </el-col>
                        <el-col class="line" :span="2">-
                        </el-col>
                    </el-form-item>

                    <el-form-item align="center">
                        <el-button type="primary" @click="editDialogForm('editFormRef')">提交</el-button>
                        <el-button @click="editDialogVisible = false">取消</el-button>
                    </el-form-item>
                </el-form>
            </template>
        </el-dialog>


    </div>
</template>

<script>
    export default {
        data() {
            // 校验规则变量定义

            return{
                // 获取日报参数列表的参数
                queryInfo:{
                    query:'',
                    page:'',
                    size: 6,
                    total: 7
                },
                reportData: [{
                    id:'',
                    title:'',
                    user:'',
                    time:'',
                    contents:'',
                    trouble:'',
                    method:'',
                    harvest:'',
                    plan:''
                }],
                // 控制添加日报对话框的显示与隐藏
                addDialogVisible: false,
                // 添加日报的表单数据
                addForm:{
                    title:'',
                    user:'',
                    time:'',
                    contents:'',
                    trouble:'',
                    method:'',
                    harvest:'',
                    plan:''
                },
                // 添加表单验证规则
                addFormRules:{
                },
                // 控制修改对话框的显示与隐藏
                editDialogVisible: false,
                // 控制修改的表单数据
                editForm:{},
                // 修改表单验证规则
                editFormRules:{}
            }
        },
        // 生命周期函数
        created() {
            this.getReportList()
        },
        methods: {
            // 获取初始化用户列表
            getReportList(){
                const _this = this
                axios.get('report/reports/0/'+ this.queryInfo.size).then(function (resp) {
                    console.log(resp)
                    _this.reportData = resp.data.content
                    _this.queryInfo.size = resp.data.size
                    _this.queryInfo.total = resp.data.totalElements
                })
            },
            // 改变页面大小
            handleSizeChange(newSize){
                const _this = this
                this.queryInfo.size = newSize;
                axios.get('report/reports/'+ this.queryInfo.page + '/' + this.queryInfo.size).then(function (resp) {
                    console.log(resp)
                    _this.reportData = resp.data.content
                    _this.queryInfo.size = resp.data.size
                    _this.queryInfo.total = resp.data.totalElements
                })
            },
            // 分页跳转事件
            currentPage(newPage) {
                const _this = this
                axios.get('report/reports/' + (newPage - 1) + '/' + this.queryInfo.size).then(function (resp) {
                    console.log(resp)
                    _this.reportData = resp.data.content
                    _this.queryInfo.size = resp.data.size
                    _this.queryInfo.total = resp.data.totalElements
                })
            },
            // 添加日报事件
            addDialogForm(addFormRef){
                const _this = this
                this.$refs[addFormRef].validate((valid) => {
                    if (valid) {
                        axios.post('report/reports/add', this.addForm).then(function (resp) {
                            console.log(resp)
                            if (resp.data == 'success') {
                                _this.$message.success('添加用户成功！！！')
                                _this.addDialogVisible = false
                                _this.getReportList()
                            }else{
                                _this.$message.error('添加用户失败！！！');
                            }
                        })
                    } else {
                        return _this.$message.error('添加用户失败！！！');
                    }
                });
            },
            // 监听添加日报对话框的关闭事件
            addDialogClosed(){
                this.$refs.addFormRef.resetFields()
            },
            // 展示修改日报的对话框
            showEditDialog(id){
                const _this = this
                axios.get('report/reports/findById'+ '/'+id).then(function (resp) {
                    console.log(resp)
                    _this.editForm = resp.data
                    _this.editDialogVisible = true
                })
            },
            // 修改对话框验证规则重置
            editDialogClosed(){
                this.$refs.editFormRef.resetFields()
            },
            // 修改日报信息
            editDialogForm(editFormRef){
                const _this = this
                this.$refs[editFormRef].validate((valid) => {
                    if (valid) {
                        axios.put('report/reports/edit', this.editForm).then(function (resp) {
                            console.log(resp)
                            if (resp.data == 'success') {
                                _this.$message.success('修改日报成功！！！')
                                _this.editDialogVisible = false
                                _this.getReportList()
                            }else{
                                _this.$message.error('修改日报失败！！！');
                            }
                        })
                    } else {
                        return false;
                    }
                });
            },
            // 根据id删除日报信息
            async removeReportById(id){
                const _this = this
                // 弹框询问是否删除数据
                const  confirmResult = await this.$confirm('此操作将永久删除该日报内容, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).catch(err => {
                    return err
                })
                // 如果用户确定删除,则返回值为字符串 confirm
                // 如果用户取消了删除,则返回值为字符串 cancel
                // console.log(confirmResult)
                if(confirmResult !== 'confirm'){
                    return this.$message.info('已取消')
                } else{
                    axios.delete('report/reports/deleteById/' + id).then(function (resp) {
                        console.log(resp)
                        _this.$message.success('删除日报成功！！！')
                        _this.getReportList()
                    })
                }
            },
            //
            queryByKey(){

            }
        },
    }
</script>

<style lang="less" scoped>
    .el-table-expand {
        line-height: 40px;
    }
    .el-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .el-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 100%;
    }
</style>